@import './../../styles/variables.scss';

.todoCheckBox{
  position: relative;
  display: flex;
  text-align: center;
}
.todoInput{
  appearance: none;
}
.todoInput::before{
  position:absolute;
  content: '';
  display: block;
  top: 10px;
  left: 0;  
  height: 17px;
  width: 17px;
  border-radius: 50%;
  border:1px solid $very-dark-grayish-blue;
}
.todoText{
  padding-left: 1.9rem;
  padding-top: .9rem;
  color: var(--font);
}
.todoTextChecked{
  padding-left: 1.9rem;
  padding-top: .9rem;
  color: var(--font);
  text-decoration: line-through;
}
.todoInput:checked::before {
  background: url("../../images/icon-check.svg") no-repeat center, linear-gradient(hsl(192, 100%, 67%), hsl(280, 87%, 65%)) ;
}